.hidden {
  display: none;
}

.breed-select {
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 100%;
  max-width: 380px;
  height: 50px;
  margin: 0;
  padding: 0 20px;
  font-size: 18px;
}

.container {
  width: 50%;
  padding: 10px;
}

.cat-info {
  width: -moz-fit-content;
  width: fit-content;
}

img {
  object-fit: cover;
  width: 100%;
  margin-top: 20px;
}

.error {
  color: red;
}

.loading {
  box-sizing: border-box;
  border-top: 3px solid #4c4a4a;
  border-right: 3px solid #0000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: 1s linear infinite rotation;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

